import React , { useState } from 'react'
import Asif12 from './image/asif.png'
import { Container , Row , Col } from 'react-bootstrap'
import InstagramIcon from '@mui/icons-material/Instagram';
import TwitterIcon from '@mui/icons-material/Twitter';
import { Link } from 'react-router-dom'
import ListIcon from '@mui/icons-material/List'
import KeyboardArrowUpIcon from '@mui/icons-material/KeyboardArrowUp';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import { Model } from './Model' 
import './main.css'
import $ from 'jquery';
export const Header = () => {
    const [show, setShow] = useState(false);
    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);
    const myFunction = (event) =>{
        if(event == 'About'){
            document.getElementById('About').style.color = 'blue';
        }
        if(event == 'Project'){
            document.getElementById('Project').style.color = 'blue';
    }
        if(event == 'Experience'){
            document.getElementById('Experience').style.color = 'blue';
    }
}
        if($(window).height() >= '712px'){
            document.getElementsByClassName('backTotop').style='margin-top: 700px !important;';
        }
    const getlocation = window.location.href;
    const [sami , setSami ]= useState(getlocation.split('3000/')[1]);
    return(
        <div className='Header'>
                <div className='Model'>
                <Modal
        show={show}
        onHide={handleClose}
        fullscreen={true}
        keyboard={false}
        className='Model12'
      >
        <Modal.Header closeButton>
            <Modal.Title><img style={{width:'88px', marginBottom:'10px' , float: 'left'}} src={Asif12} />
            <h2 style={{float:'left', marginTop:'20px', marginBottom:'10px'}}>Abdul-Sami</h2>
          </Modal.Title>
        </Modal.Header>
        <Modal.Body>
            <ul style={{listStyleType:'none', textAlign: 'center'}} className='listUnderUl'>
                <li>
                    <Link style={{textDecoration:'none' , fontSize:'22px', marginBottom:'4px', color:'black'}} to='/About'>About</Link>
                </li>
                <li>
                    <Link style={{textDecoration:'none' , fontSize:'22px', marginBottom:'4px', color:'black'}} to='/Project'>Project</Link>
                </li>
                <li>
                    <Link style={{textDecoration:'none' , fontSize:'22px', marginBottom:'4px', color:'black'}} to='/Experience'>Experience</Link>
                </li>
            </ul>
        </Modal.Body>
      </Modal>
              </div>
            <Container>
                <Row>
                    <Col md={6} sm={2}><img className='menu' style={{width:'88px', marginBottom:'10px'}} src={Asif12} /></Col>
                    <Col md={3} sm={5} style={{margin:'0px' , padding:'0px'}}>
                        <div className='menu123' id='menuicon'>
                            <div style={{float:'left'}}>
                                <Link  style={{textAlign:'left'}} onClick={myFunction()} to='/About' id='About' className='menu'>About</Link>
                            </div>
                            <div style={{float:'left'}}>
                                <Link style={{textAlign:'left'}} onClick={myFunction()} to='/Project' id='Project' className='menu'>Project</Link>
                            </div>
                            <div style={{float:'left'}}>
                                <Link onClick={myFunction()} to='/Experience' id='Experience' className='menu'>Experience</Link>
                            </div>
                    </div>
                        </Col>
                        <Col className='icons' md={3} sm={3}>
                        <div className='menu' >
                            <div className='face'>
                                f
                            </div>
                            <div className='insta'>
                                in
                            </div>
                            <div className='in'>
                                <InstagramIcon id='insta' className='instagram' />
                            </div>
                            <div className='twitter'>
                                <TwitterIcon id='twit' className='twit' />
                            </div>
                        </div>
                        </Col>
                </Row>
                <Row>
                    <Col md={12}>
                    <img className='mediaqu' style={{width:'60px', marginTop:'-20px'}} src={Asif12} />
                    <div className='list' onClick={handleShow}>
                        <ListIcon className='ListIcon' />
                    </div>
                    </Col>
                </Row>
                </Container>    
                <div id='backtotop' className='backTotop'>
                    <KeyboardArrowUpIcon className='backTotopicon' />
                </div>       
        </div>
    )
}